import * as React from "react"
import { Link } from "gatsby"

import * as styles from "../styles/content.module.css"
import Footer from "./footer"

// import { MDXRenderer } from "gatsby-plugin-mdx"
// import { defineCustomElements as deckDeckGoHighlightElement } from "@deckdeckgo/highlight-code/dist/loader"
// deckDeckGoHighlightElement()
import "gatsby-remark-vscode/styles.css"

const Content = ({ content }) => {
  console.log(content, 666)
  const title = content.frontmatter.title

  const prev = content.previous ? (
    <Link to={content.previous.frontmatter.path}>
      {`上一篇：` + content.previous.frontmatter.title}
    </Link>
  ) : (
    ""
  )
  const next = content.next ? (
    <Link to={content.next.frontmatter.path}>
      {`下一篇：` + content.next.frontmatter.title}
    </Link>
  ) : (
    ""
  )

  return (
    <div className={styles.content}>
      <div className={styles.titleBox}>
        <h1>{title}</h1>
        <p className={styles.desc}>Date：{content.frontmatter.date}</p>
      </div>
      {/*<MDXRenderer>{content.body}</MDXRenderer>*/}
      <div className={styles.md}>
        <div dangerouslySetInnerHTML={{ __html: content.html }} />
      </div>
      <div className={styles.pageBox}>
        <div>{prev}</div>
        <div>{next}</div>
      </div>
      <Footer></Footer>
    </div>
  )
}

export default Content
