import * as React from "react"
import { Link } from "gatsby"
import * as styles from "../styles/sidebar.module.css"

/*
  sidebar={
    pageTitle: 'git教程'
    nodes: {
        'git初级': [
          {
            title: '',
            path: '',
            id: ''
          },
          ...
        ]
      },
      ...
  }
   */

const Sidebar = ({ sidebar, title }) => {
  sidebar = sidebar || {}
  let sidebarData = {
    pageTitle: "",
    nodes: {},
  }

  sidebarData.pageTitle = sidebar[0].node.frontmatter.pageTitle

  sidebar.forEach(nodes => {
    const frontmatter = nodes.node.frontmatter
    const parentTitle = frontmatter.parentTitle
    if (!(parentTitle in sidebarData.nodes)) {
      sidebarData.nodes[parentTitle] = []
    }

    sidebarData.nodes[parentTitle].push({
      title: frontmatter.title,
      path: frontmatter.path,
      id: nodes.node.id,
    })
  })

  return (
    <div className={styles.sidebar}>
      <h2 className={styles.mainTitle}>
        <span className={styles.sqrt}></span>
        {sidebarData.pageTitle}
      </h2>

      <ul className={styles.firstTitleList}>
        {Object.keys(sidebarData.nodes).map(parentTitle => (
          <li key={parentTitle}>
            <span className={styles.link}>
              {parentTitle}({sidebarData.nodes[parentTitle].length})
            </span>

            <ul className={styles.secondTitleList}>
              {sidebarData.nodes[parentTitle].map(children => (
                <li key={children.id}>
                  <Link
                    to={children.path}
                    className={[
                      styles.link,
                      children.title === title ? styles.current : null,
                    ].join(" ")}
                  >
                    {children.title}
                  </Link>
                </li>
              ))}
            </ul>
          </li>
        ))}
      </ul>
    </div>
  )
}

export default Sidebar
