import * as React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"

export const query = graphql`
  query BlogPageQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      frontmatter {
        date
        description
        group
        pageTitle
        parentTitle
        path
        title
      }
      html
      tableOfContents(maxDepth: 2)
    }
    allMarkdownRemark(sort: { fields: frontmatter___order, order: ASC }) {
      group(field: frontmatter___group) {
        edges {
          node {
            frontmatter {
              path
              title
              parentTitle
              pageTitle
              group
            }
            id
          }
          next {
            frontmatter {
              title
              path
            }
          }
          previous {
            frontmatter {
              title
              path
            }
          }
        }
      }
    }
  }
`

const Post = ({ data }) => {
  let post = data.markdownRemark
  const thisPageTitle = post.frontmatter.title
  const thisPageGroup = post.frontmatter.group
  const allPost = data.allMarkdownRemark

  // 左侧目录
  let sidebarData = {}
  allPost.group.forEach(j => {
    const nodesGroup = j.edges[0].node.frontmatter.group

    if (thisPageGroup === nodesGroup) {
      sidebarData = j.edges
    }

    // 上一篇，下一篇
    j.edges.forEach(edge => {
      const title = edge.node.frontmatter.title
      if (thisPageTitle === title) {
        post.next = edge.next
        post.previous = edge.previous
      }
    })
  })

  // 右侧目录
  const catalogue = post.tableOfContents || ""

  const seo = {
    title: post.frontmatter.title,
    description: post.frontmatter.description,
  }

  return (
    <Layout
      sidebarData={sidebarData}
      contentData={post}
      catalogue={catalogue}
      seo={seo}
    ></Layout>
  )
}

export default Post
